import request from '@/utils/request';
var api = {
  GetAll: '/api/terminal/CmdField/GetAll',
  TemplateGetAll: '/api/terminal/SensorTemplate/GetAll',
  GetById: '/api/terminal/SensorTemplate/GetById'
}; // 获取全部指令字段

export function GetAll() {
  return request({
    url: api.GetAll,
    method: 'get'
  });
} // 获取全部传感器字段映射模板

export function TemplateGetAll() {
  return request({
    url: api.TemplateGetAll,
    method: 'get'
  });
} // 根据ID获取传感器字段映射模板

export function GetById(id) {
  return request({
    url: api.GetById,
    method: 'get',
    params: {
      id: id
    }
  });
}